:root {
    --active-tab-color: #894adc;
    --white-color: #FFF;
}

.active__tab {
    background-color: var(--active-tab-color);
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 4px;
    border-radius: 5px;
}

.active__tab h6 {
    color: var(--white-color);
}

/* Resposnive */

@media(max-width: 600px) {
    .__admin__side__bar__menu{
        display: none;
    }

    .conntent__right__side__menu__links__content{
        padding-left: 0px!important;
    }
}